@import url(https://fonts.googleapis.com/css?family=Russo+One);
body{background-color:#099}.fa:hover{color:white}#editor-wrap{width:50%;margin:25px auto auto auto}#editor-wrap #editor-toolbar{font-family:"Russo One", "monospace";position:relative;border:1px solid black;padding-right:4px;width:100%;background-color:#066}#editor-wrap #editor-toolbar #logo{margin:auto auto auto 5px}#editor-wrap #editor-toolbar #expand-editor{cursor:pointer;position:absolute;right:2px}#editor-wrap #editor{outline:none;font-size:20px;box-shadow:0 10px 20px #000;margin-top:0px;background-color:#9cf;border:1px solid black;resize:vertical;width:100%;margin-bottom:0px;min-height:200px}#previewer-wrap{width:50%;margin:25px auto auto auto;width:80%;margin-top:10px}#previewer-wrap #previewer-toolbar{font-family:"Russo One", "monospace";position:relative;border:1px solid black;padding-right:5px;width:100%;background-color:#066}#previewer-wrap #previewer-toolbar #logo{margin:auto auto auto 5px}#previewer-wrap #previewer-toolbar #expand-previewer{cursor:pointer;position:absolute;right:2px}#previewer-wrap #preview{padding-left:5px;background-color:#9cf;font-size:20px;box-shadow:0 10px 20px #000;width:100%;border:1px solid black;min-height:300px}#previewer-wrap #preview h1{border-bottom:solid}#previewer-wrap #preview h2{border-bottom:2px solid}#previewer-wrap #preview a{text-decoration:underline}#previewer-wrap #preview code{display:inline-block;background-color:white}#previewer-wrap #preview blockquote{padding-left:3px;border-left:5px solid}#previewer-wrap #preview table{border-collapse:collapse}#previewer-wrap #preview table,#previewer-wrap #preview th,#previewer-wrap #preview td{border:1px solid}

